<template>
    <div :class="`liquidity-provider-pool ${getColor(pool.risk)}`">
        <div class="liquidity-provider-pool-cell cell-1">
            <div class="images-block">
                <img class="image image-1" :src="require(`@/assets/newImages/coins/vectors/${pool.leftCoinTicker}.svg`)">
                <img class="image image-2" :src="require(`@/assets/newImages/coins/vectors/${pool.rightCoinTicker}.svg`)">
            </div>

            <div class="text-block">
                <div class="title">
                    {{$localizeFilter('coins_pair')}}
                </div>
                <div class="text bold">
                    {{`${pool.leftCoinTicker} / ${pool.rightCoinTicker}`}}
                </div>
            </div>
        </div>

        <div class="liquidity-provider-pool-cell cell-2">
            <div class="text-block">
                <div class="title">
                     {{$localizeFilter('interest')}}
                </div>
                <div class="text" :class="presale ? 'presale-grey': ''">
                    {{`${pool.interest} %`}}
                </div>
            </div>
        </div>

        <div class="liquidity-provider-pool-cell cell-3">
            <div class="text-block">
                <div class="title">
                    {{$localizeFilter('risk')}}
                </div>
                <div class="text" :class="presale ? 'presale-grey': ''">
                    {{`${pool.risk}%`}}
                </div>
            </div>
        </div>

        <div class="liquidity-provider-pool-cell cell-4">
            <div class="text-block">
                <div class="title">
                    {{$localizeFilter('capacity')}}
                </div>
                <div class="text" :class="presale ? 'presale-grey': ''">
                    {{`${pool.capacity}%`}}
                </div>
            </div>
        </div>

        <div class="liquidity-provider-pool-cell cell-5">
            <div class="text-block" v-if="presale">
                <div class="title">
                    {{$localizeFilter('left_to_start')}}:
                </div>
                <div class="text yellow-text-timer">
                    {{`${leftToStartTimeString} `}}
                </div>
            </div>
            <div class="text-block" v-else>
                <div class="title">
                    {{$localizeFilter('deposited')}}
                </div>
                <div class="text" :class="((pool.depositedLeftCoin > 0 || pool.depositedRightCoin > 0) && pool.poolType=='crypto_fiat_pool') ? 'deposited-cryptofiat' : ''">
                    {{`${pool.depositedLeftCoin}&nbsp;${pool.leftCoinTicker}&nbsp;/ ${pool.depositedRightCoin}&nbsp;${pool.rightCoinTicker}`}}
                </div>
            </div>
        </div>


        <div class="liquidity-provider-pool-cell cell-6">
            <div class="buttons-block">
                <div class="basic-button" v-if="pool.auctionRunning" @click="openAuction()">{{$localizeFilter('auction')}}</div>
                <div class="basic-button" v-else @click="deposit">{{$localizeFilter('liquidity_deposit')}}</div>
                <div class="underline-button" @click="withdraw" v-if="!presale">{{$localizeFilter('liquidity_withdraw')}}</div>
                <div class="deposited" v-if="pool.deposited">–í–Ω–µ—Å–µ–Ω–æ:&nbsp;{{$utils.formatNumber(pool.deposited / 1000000, 'en-US', 0, 2)}}&nbsp;USDT</div>
            </div>
        </div>

        <div class="presale" v-if="presale">{{ $localizeFilter('presale')  }}</div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        pool: {
            type: Object,
            required: true
        }
    },
    watch: { 
        pool: {
            handler: function (newVal, oldVal) {
               this.$nextTick(this.restartInterval())
            },
            deep: true
        }
    },
    data: ()=> ({
        leftToStartTimeString: "",
        interval: null
    }),
    methods: {
        openAuction() {
            this.$router.push(`/cabinet/auction?poolId=${this.pool.poolId}&currency1=${this.pool.leftCoinTicker}&currency2=${this.pool.rightCoinTicker}`)
        },
        deposit() {
            this.$emit('deposit', this.pool)
        },
        withdraw() {
            this.$emit('withdraw', this.pool)
        },
        getColor(risk) {
            if (risk < 2) { return 'risk-green' }
            else if (risk < 3.5) { return 'risk-yellow' }
            else if (risk < 5) { return 'risk-orange' }
            else { return 'risk-red' }
        },
        getLeftToStartTimeString() {
            if (this.pool.launchTimestamp === null) { return '' }
            let leftToStartTime = this.pool.launchTimestamp - Date.now();
            let days = Math.floor(leftToStartTime / (1000 * 60 * 60 * 24));
            let hours = Math.floor((leftToStartTime % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
            let minutes = Math.floor((leftToStartTime % (1000 * 60 * 60)) / (1000 * 60));
            let seconds = Math.floor((leftToStartTime % (1000 * 60)) / 1000);
            if (hours < 10) { hours = `0${hours}` }
            if (minutes < 10) { minutes = `0${minutes}` }
            if (seconds < 10) { seconds = `0${seconds}` }
            return `${days}:${hours}:${minutes}:${seconds}`
        },
        restartInterval() {
            clearInterval(this.interval);
            this.interval = null;
            if (this.presale) {
                this.leftToStartTimeString = this.getLeftToStartTimeString();
                this.interval = setInterval(() => {
                    this.leftToStartTimeString = this.getLeftToStartTimeString();
                }, 1000);
            }
        }
    },
    computed: {
        presale(){
            // return false;
            return this.pool.poolType=='crypto_fiat_pool' && this.pool.poolId == 32; // && this.pool.launchTimestamp > +Date.now();
        },
    },
    mounted() {
        this.restartInterval()
    },
    unmounted() {
        clearInterval(this.interval);
        this.interval = null;
    }
})
</script>

