            <div class="new-modal-back-button-container">
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            </div>
            <div class="modal-content">
                <div class="title">{{$localizeFilter('fill_up_a_wallet')}} {{coin.coinTicker}}</div>
                <div class="fill-wallet">{{$localizeFilter('wallet_address')}}</div>
                <div class="input-field disabled">
                    <input id="wallet" class="custom-input" type="text" v-model.trim="fillWalletAddress"/>
                    <button class="custom-input-button copy-button" @click="copy(fillWalletAddress)"><i class="material-icons">content_copy</i></button>
                </div>
                <div class="network">{{$localizeFilter('network')}}</div>
                <div class="flex-row">
                    <div class="network-name">{{ fullNetwork }}</div>
                    <!-- <button class="change-network" @click="changeNetwork">{{$localizeFilter('change')}}</button> -->
                </div>
                <!-- <div class="min-sum-title">{{$localizeFilter('wallet_fill_min')}}</div>
                <div class="min-sum-amount">{{`${minimalFillAmount} ${coin.coinTicker}`}}</div> -->
            </div>
        </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '../../filters/localize.filter'

export default defineComponent({
  props: {
    //   coin: {
    //       type: Object,
    //       required: true
    //   },
  },
  setup() {
    
  },
  data: ()=>({
        network: 'TRON',
        fullNetwork: 'Tron(TRC20)',
        coin: {coinTicker: 'USDT'},
        fillWalletAddress: ''
  }),
  computed: {
        // fillWalletNetwork() {
        //     return this.$store.getters.fillWalletNetwork
        // },
        // fillWalletAddress() {
        //     return this.$store.getters.fillWalletAddress?.walletAddress
        // },
        // minimalFillAmount() {
        //     return this.$store.getters.fillWalletAddress?.minimalFillAmount || 0
        // },
  },
  methods: {
      closeModal(reload) {
          this.$emit('closeModal', reload)
      },
    //   changeNetwork() {
    //     this.$emit('changeNetwork')
    //   },
      copy(value) {
            function fallbackCopyTextToClipboard(text) {
                var textArea = document.createElement("textarea");
                textArea.value = text;
                
                // Avoid scrolling to bottom
                textArea.style.top = "0";
                textArea.style.left = "0";
                textArea.style.position = "fixed";

                document.body.appendChild(textArea);
                textArea.focus();
                textArea.select();
                var successful;
                try {
                    successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    // console.log('Fallback: Copying text command was ' + msg);
                } catch (err) {
                    console.error('Fallback: Oops, unable to copy', err);
                }

                document.body.removeChild(textArea);
                return successful;
            }
            if (value) {
                const clipboardData =
                // event.clipboardData ||
                window.clipboardData ||
                // event.originalEvent?.clipboardData ||
                navigator.clipboard;
                try {
                    clipboardData.writeText(value); 
                    this.$message(localizeFilter('copied')) 
                } catch (e) {
                    if(fallbackCopyTextToClipboard(value)){
                        this.$message(localizeFilter('copied')) 
                    }
                }
            }
      }
  },
  async mounted() {
    try {
          const data = {coinTicker: this.coinTicker, network: this.network}
          const { walletAddress } = await this.$store.dispatch('getLiquidityProviderDepositWalletAdddress', data)
          this.fillWalletAddress = walletAddress
        } catch(e) {
          this.$error(e)
        }
    }
})
</script>


