<template>
    <div class="toggle-block">
    <label class="title" v-if="title">{{title}}</label>
    <label class="text text-left" v-if="hideVariantsText !== true" :class="value==0 ? 'selected': ''">{{$localizeFilter((prefix || '') + variants[0])}}</label>
    <div class="toggle-container" :class="value==0 ? 'left-side' : 'right-side'" @click="toggle">
        <div class="toggle" :class="value==0 ? 'left-side' : 'right-side'"></div>
    </div>
    <label class="text text-right" :class="value==1 ? 'selected': ''" v-if="hideVariantsText !== true"> {{$localizeFilter((prefix || '') + variants[1])}}</label>
    </div>
</template>

<!-- :colors="['#20958B', '#FF9400']"  -->
    
    <script>
    import { defineComponent } from '@vue/composition-api'
    
    export default defineComponent({
       props: ['title','variants','selected', 'hideVariantsText', 'prefix'],
       watch: { 
                selected: function(newVal, oldVal) {
                    if (newVal != null && newVal!= undefined) {
                    this.value = this.variants.indexOf(newVal)
                }
            }
        },
      data: ()=> ({
          value: 0
      }),
      methods: {
        toggle() {
              this.value=!this.value
              this.$emit('toggle', Number(this.value))
        }
      },
    
    })
    </script>
    
    <style scoped>
    .toggle-container {
      display: inline-block;
      width: 38px;
      height: 16px;
      border-radius: 5px;
      position: relative;
      margin: 0 5px 0 5px;
    }
    
    * {
        color: #EAEAEA;
    }
    
    .toggle {
      content: ""!important;
      width: 16px;
      height: 10px;
      border-radius: 3px;
      position: absolute;
      top: 1px;
      transition: all 0.1s linear;
    }
    
    .toggle:hover {
        filter: opacity(0.7);
    }
    
    .toggle.left-side {
          left: 1px;
    }
    
    .toggle.right-side {
          left: 17px;
    }
    .toggle-block {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
    }
    
    .title {
        margin-right: 10px;
        white-space: pre-line;
    }
    
    .title, .text {
        font-size: 16px;
        line-height: 19px;
        
    }
    
    .text.selected {
        font-weight: bold;
    }

    .toggle-container.left-side {
        border: 2px solid #20958B;
    }

    .toggle-container.right-side {
        border: 2px solid #FF9400;
    }

    .toggle.left-side {
        background: #20958B;
    }

    .toggle.right-side {
        background: #FF9400;
    }

    .text-right.selected {
        color: #FF9400;
        font-weight: 500;
    }
    </style>
    