<template>
    <div class="modal-layout">
        <div class="darken" @click="closeModal">
            <div class="new-modal gift-modal" :class="view" @click.stop="(showDropdown=false)">
                <!-- @click.stop="(showDropdown=false)" -->
                <div class="new-modal-back-button-container">
                    <button class="new-modal-back-button thin" @click="closeModal"></button>
                </div>
                <div class="modal-content">
                    <div class="window-title">{{$localizeFilter('send_credit_deposit_title')}}</div>

                    <div class="window-text">{{$localizeFilter('send_credit_deposit_text')}}</div>

                    <div class="gift-options-block" @click.stop="">
                        <CreditDepositOptions class="gift-options" v-if="showOptions" :options="options" @showDropdownMethod="showDropdownMethod" @selectOption='selectOption' @clearRecipientId="clearRecipientId" :showDropdown="showDropdown"/>
                    </div>

                    <!-- <div class="input-field">
                        <input id="amount" class="custom-input" :placeholder="$localizeFilter('amount_in', ['USDT'])" type="text" v-model.trim="amount"  :class="{ invalid: v$.amount.$error }"/>
                        <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                            {{ error.$message }}
                        </small>
                    </div> -->

                    <div class="window-text" :style="'margin-top: 20px;'">{{$localizeFilter('send_credit_deposit_text_amount')}}</div>

                    <div class="window light input-block">
                        <!-- <label style="color: #EAEAEA;">{{}}</label> -->
                        <input id="amount" class="recipient-name-input" :placeholder="$localizeFilter('enter_amount_usdt')" type="text" v-model.trim="amount"/>
                        <!-- <button class="trigger-button">
                            <img style="width:10px; height: 6px;" :src="require('@/assets/newImages/white_arrow.png')">
                        </button> -->
                    </div>

                    <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                        {{ error.$message }}
                    </small>

                    <div class="amount-hint">{{$localizeFilter('available_for_sending_credit_deposit', [$utils.formatNumber(maxAmount, 'en-US', 0, 6) + ' USDT'])}}</div>

                    <div class="button-block">
                        <button class="action-button" @click="sendCreditDeposit" :disabled="referralId === null">
                            {{$localizeFilter('send_deposit')}}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import CreditDepositOptions from '../ReferralCreditDepositOptions.vue';
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue, maxValue, minLength, maxLength } from "@vuelidate/validators";

export default defineComponent({
    props: {
        button: {
            type: Object,
            default: null
        },
        view: {
            type: String,
            default: "center" // 'center', 'full'
        },
        testBalance: {
            type: Number,
            default: 0
        },

    },
    components: { CreditDepositOptions },
    validations() {
        return {
            amount: {
                // required: helpers.withMessage(({ 
                //     $pending,
                //     $invalid,
                //     $params,
                //     $model
                // })=>`${$pending} ${$invalid} ${$params} ${$model}`, required),
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required),
                minValue: helpers.withMessage(localizeFilter('min_value', [this.minAmount]), minValue(this.minAmount)),
                maxValue: helpers.withMessage(localizeFilter('not_enough_funds_on_balance',['USDT']), maxValue(this.maxAmount)),
            },
        };
    },
    setup() {
        return { v$: useVuelidate() };
    },
    data: () => ({
        show: 1,
        showOptions: true,
        options: [],
        referralId: null,
        showDropdown: false,
        amount: null,
        minAmount: 0
    }),
    methods: {
        showDropdownMethod() {
            this.showDropdown = true
        },
        clearRecipientId()  {
            this.referralId = null
        },
        closeModal() {
            this.$emit("closeModal");
        },
        selectOption(id) {
            this.referralId = id
            this.showDropdown = false
            // console.log('selectOption')
        },
        async sendCreditDeposit() {
            this.v$.$touch()
            if (this.v$.error) { return }
            const data = {referralId: this.referralId, amount: Number(this.amount * 1000000)}
            try {
                await this.$store.dispatch('sendSponsorCreditDeposit', data)
                this.$message(localizeFilter('referral_credit_deposit_successfully_sent', [this.referralId]))
                this.closeModal()
            }
            catch (e) {
                this.$error(e)
            }
        }
    },
    async mounted() {
        try {
            this.options = await this.$store.dispatch('getReferralsForSponsorCreditDeposit')
            // console.log(this.options)
        }
        catch (e) {
            this.$error(e)
        }
    },
    computed: {
        maxAmount() {
            return this.testBalance / 1000000.0
        }
    }
})
</script>

