<template>
    <div class="liquidity-provider-toggle-outer-container">
    <label class="title" v-if="title">{{title}}</label>
    <div class="liquidity-provider-toggle-flex-container" :style="`height: ${height}px;`">
        <div class="liquidity-provider-toggle-flex" v-for="(variant, index) in variants" :key="index">
            <div class="liquidity-provider-toggle" :class="{selected: index === value}" @click="toggle(index)">
                <span class="liquidity-provider-toggle-text">{{$localizeFilter(variant)}}</span>
            </div>
            <div class="line"></div>
        </div>
    </div>
    </div>
    </template>
    
    <script>
    import { defineComponent } from '@vue/composition-api'
    
    export default defineComponent({
      props: ['title','variants','selected'],
       watch: { 
            selected: function(newVal, oldVal) {
                if (newVal != null && newVal!= undefined) {
                    this.value = this.variants.indexOf(newVal)
                }
            }
        },
      data: ()=> ({
          value: 0,
          height: 0
      }),
      methods: {
          toggle(newVal) {
              this.value=newVal
              this.$emit('toggle', Number(this.value))
          }
      },
      mounted() {
        if (window.innerWidth > 768) {
            this.height = 37
        } else {
            this.height = 37 * this.variants.length - this.variants.length + 1
        }
      }
    
    })
    </script>
    
    <style scoped>
